<br>1.2.锋伟网络不保证所提供的产品、服务不受任何干扰，及时、安全、可靠或不出现错误；
<br>1.3.鉴于技术本身的局限性，即使锋伟网络提供的产品、服务等已经过相应测试，但锋伟网络不保证其余其他软硬件、系统完全兼容。如出现不兼容的情况，用户可将情况告知锋伟网络，已获得技术支持。
