<br>4.用户使用锋伟网络服务所取得的任何资料，其风险由用户自行承担，因该使用而导致用户电脑系统损坏或资料流失的，用户应负完全责任。
<br>5.用户基于以下原因而造成的利润、商业信誉、资料损失或其他有形或无形的损失，锋伟网络不承担任何直接、间接、连带、附带、衍生或惩罚性的赔偿：
<br>5.1.经任何自锋伟网络而获得的他人的信息、内容或广告宣传等任何资讯，用户应当自行甄别真伪；
