<br>6.用户理解并同意用户为自行选择使用锋伟网络之相关服务，风险自负，包括但不限于用户使用本服务过程中的行为，以及因使用锋伟网络服务产生的一切后果。</div><br>

<div class="title">八、服务的变更、中断、终止</div>
      <div class="text blod">1.您理解并同意，锋伟网络基于经营策略的，可能会对所提供的服务内容进行变更，也可能会中断、中止或终止服务。锋伟网络对用户或任何第三人均无需承担任何责任。
<br>2.您理解并同意，如锋伟网络发现您在使用本网站及本服务时存在违反法律法规、本协议及其他锋伟网络制定的规则、社会公德、公序良俗和/或侵害他人合法权益等情形，锋伟网络有权不经通知而单方中断或终止向您提供全部或部分服务，且有权向您采取包括但不限于追究法律责任、索赔全部损失等措施。
<br>3.锋伟网络终止向您提供服务后，有权根据适用法律的要求删除您的个人信息，亦有权依照法律法规规定的期限和方式继续保留您留存在锋伟网络的其他内容和信息。</div><br>

<div class="title">九、隐私政策</div>
      <div class="text">1.锋伟网络注重保护用户的个人信息及个人隐私，您在浏览、注册、登陆、使用锋伟网络的产品/或服务时，锋伟网络将按照平台公布的《隐私政策》约定处理和保护您的个人信息。
<br>2.您应当在仔细阅读、充分理解《隐私政策》后使用锋伟网络的产品/或服务，如您不同意隐私政策的内容，将可能导致锋伟网络的产品/或服务无法正常运行。您使用或继续使用锋伟网络的产品/或服务的行为，都表示您充分理解和同意《隐私政策》（包括更新版本）的全部内容。</div><br>

<div class="title">十、法律适用及管辖</div>
      <div class="text">1.本协议的生效、履行、解释及争议的解决均适用中华人民共和国法律。本条款因与中华人民共和国现行法律相抵触而导致部分无效，不影响其他部分的效力。双方同意，解决争议时，应以您同意的最新版《锋伟网络用户协议》为准。
<br><b>2.本协议的签署地为中华人民共和国深圳市南山区，如就本协议内容或其执行发生任何争议，应尽量友好协商解决；协商不成时，则争议各方均一致同意将争议提交至协议签署地人民法院提交诉讼解决。</b></div><br>

<div class="title">十一、其他</div>
