<template>
  <div class="main">
<div class="right">
  <!-- <div id="select-message">
    <select name="model_name" id="model_name" style="width:150px;font-size: 18px;">
      <option value="Ziwei">Ziwei</option>
      <option value="gpt4">gpt4</option>
      <option value="gpt3.5">gpt3.5</option>
    </select>
  </div> -->
  
  <div id="chat-container"  v-loading="showLoading">
    <div v-for="(item,index) in chatList" class="message-div" :key="index">

      <span v-if ="item.role == 'user'" class="user-icon"><img src="@/assets/img/user.png"></span>
      <span v-if ="item.role == 'user'" class="user-message">{{ item.content }}</span>
      <span v-if ="item.role == 'assistant'" class="bot-icon"><img src="@/assets/img/bot.png"></span>
      <span v-if ="item.role == 'assistant'" class="bot-message">{{ item.content.replaceAll('###','').replaceAll('##','').replaceAll('**','').replaceAll('---','') }}</span>
    </div>
  </div>
  <div id="input-message">
    <!-- <img class="clear-img" @click="clearChat()" src="@/assets/img/clear.png" alt=""> -->
    <el-input
      placeholder="请输入您的问题，回车发送"
      v-model="message"
      type="textarea"
      :rows="4"
      @keyup.enter.native="sendMessageE"
      clearable>
    </el-input>

    <!-- <div style="width: 15px;"></div>-->
    <div class="send-box"><el-button class="button-up" :disabled="buDisabled" @click="sendMessage()"><img src="@/assets/img/send.png" alt=""></el-button></div>
    <!--<el-button type="warning" class="padding-0" >Clear</el-button> -->
  </div>
</div>
</div>
</template>
<script>
import { trans_pic } from '@/api/hsyq/index'
export default {
    name: 'HistoryChatbot',
    props: {
       words: String,
    },
    data(){
        return{
        chatList:[],
        message:'',
        model_name:'Ziwei',
        buDisabled:false,
        showLoading:false,
        examImg:[false,false,false]
        }
    },
    created(){
      this.reset()
      
    },
    mounted(){
  
    },
    methods: {
    reset(){
      console.log('reset')
      this.message = ''
       this.chatList= [
        {
          "role": "system",
          "content": "You are an English teacher, please use English for all the following conversations. Help me evaluate the article I posted in English"
        },
        {
          "role": "user",
          "content": this.words
        },]
    },
    sendMessageE(e){
      if(!e.altKey && !e.shiftKey && !e.ctrlKey && !e.metaKey){
        this.sendMessage()
      }
    },
    sendMessage(){
      if(this.message == '') return
      this.chatList.push({
        "role": "user",
        "content": this.message
      })
      
      this.initMessage()
    },
    transPic(index){
      this.examImg[index] = true
    },
    initMessage() {
    this.buDisabled = true;
    this.showLoading = true;
    // {
    //   "role": "assistant",
    //   "content": "Of course! I'm here to help you with any questions or concerns you may have about English. What can I assist you with today?",
    //   "traceId": "b2f526acbe396168379d07ec4fa8ad47",
    //   "status": 1
    // },
    // {
    //   "role": "user",
    //   "content": "About Me\nMy name is LiHua. I am a girl. I am ten years old. I was born in summer. My birthday is in June. There are four people in my family, my father, my mother, my older brother and me. My father is a police man. My mother is a nurse. My brother and I are students. I love my family very much."
    // },
    // {
    //   "role": "assistant",
    //   "content": "Hello, LiHua! It's great to learn more about you. You have a lovely family, and it's wonderful that you love them so much. If you have any questions about English or need help with anything, feel free to ask. I'm here to assist you!",
    //   "traceId": "a9087d58586c64e3f995297bd5927c21",
    //   "status": 1
    // },
    // {
    //   "role": "user",
    //   "content": "帮我评价一下我刚才的文章"
    // }
    let json = {
        "prompt": "",
        "max_tokens": 2048,
        "temperature": 0.3,
        "top_p": 0.9,
        "model": "yi-lightning",
        "messages": this.chatList.slice(-50),
        "tools": null
      }
      this.message = ''
      trans_pic(json).then(res=>{
          this.chatList.push(res.choices[0].message)
          this.buDisabled = false
        this.showLoading= false
        
        })
    },
    clearChat() {
      this.chatList=[]
    }
    },

}
</script>
<style scoped>
   body {
        font-family: 'Helvetica Neue', Arial, sans-serif;
        margin: 0;
        padding: 0;
        color:#333333;
    }
    .main{
      width: 1080px;
      padding: 0!important;
      margin: 0 auto;
    }
    .upload-img{
      position: absolute;
      /* bottom: -140px;  */
      bottom: 0px; 
      text-align: left;
      transition: bottom 0.5s ease; 
      width: 100%;
      height: 15%;
      padding: 5px 5px 0 5px;
      background: #fff;
    }
    /* .left:hover .upload-img{
      bottom: 0;
    } */
    .upload_warp_img_div_top {
		  position: absolute;
		  top: 0;
		  width: 100%;
		  height: 30px;
		  background-color: rgba(0, 0, 0, 0.4);
		  line-height: 30px;
		  text-align: left;
		  color: #fff;
		  font-size: 12px;
		  text-indent: 4px;
		}
		.upload_warp_img_div_del {
		  position: absolute;
		  top: 6px;
		  width: 16px;
		  right: 4px;
		}
		.upload_warp_img_div_text {
		  white-space: nowrap;
		  width: 80%;
		  overflow: hidden;
		  text-overflow: ellipsis;
		}
    .upload_warp_left img {
		  margin-top: 25px;
      width: 30px;
		}

		.upload_warp_left {
		  float: left;
		  width: 80px;
      background: #F5F6F8;
		  height: 80px;
		  cursor: pointer;
      overflow: hidden;
      text-align: center;
      border-radius: 5px;
		}
    .upload_warp_button{
      display: table-cell;
      position: absolute;
      right: 1px;
      top: 57px;
    }
    .text1{
      font-size: 12px;
      font-weight: 700;
      padding-left: 2px;
    }
   >>> #input-message .el-button{
      border-radius:20px;
    }
    .send-box{
      position: absolute;
      background: #fff;
      right: 0;
      bottom: 0;
      padding: 0 15px 0 0;
    }
    .button-up{
      background-image:linear-gradient(106.92deg, #10c5fe 14.83%, #1093fe 31.52%, #4f4fe7 65.11%, #b60cb9 91.29%, #e0caf5 102.83%)!important;
      color: #ffffff!important;
      
      padding: 8px 18px!important;

    }
    .button-up img{
      width: 20px;
      height: auto;
    }
    .upload_warp_img_div{
      position: relative;
		  line-height: 80px;
      width: 80px;
      background: #F5F6F8;
      border-radius: 5px;
      overflow: hidden;
		  height: 80px;
		  cursor: pointer;
		  display: table-cell;
		  text-align: center;
		  background-color: #eee;
		  cursor: pointer;
    }
    .upload_warp_img_div .myimg {
		  width: 100%;
		  height:100%;
		}
    .upload_warp_img_div .select_img{
      position: absolute;
      width: 100%;
		  height:100%;
      left: 0;
      z-index:9;
      top: 0;
      font-size: 30px;
      color: rgb(41, 229, 41);
      text-align: center;
      background-color: rgba(0,0,0,0.2);
    }
    #player1{
      margin-top: 20px;
      display: block;
      max-height: 85%;
      margin: 0 auto;
    }
    .margin_l_10{
      left: 10px;
    }
    .margin_l_20{
      left: 20px;
    }
    .margin_l_30{
      left: 30px;
    }
    .right{
      width: 100%;
      margin: 0px auto;
      background: #fff;
      border-radius:20px;
      padding-bottom:15px;
      overflow: hidden;
    }
    >>> .right .el-textarea__inner{
      border: none ;
      padding:5px 80px 5px 15px ;
    }
    >>> .right .el-textarea{
      font-size: 16px ;
    }
    #chat-container {
        width: 100%;
        height: 550px;
        margin: auto;
        margin-top: 5px;
        border-bottom: 1px #f2f3f5 solid;
        padding: 12px;
        /* box-shadow: 0 0 10px rgba(149,29,203,0.15); */
        overflow-y: scroll;
    }

    #select-message {
        width: 90%;
        margin: auto;
        margin-top: 30px;
    }

    #select-message select {
        width: 25%;
        height: 60px;
        font-size:18px;
        padding: 10px;
        border-radius: 8px;
        margin-right: 5%;
    }

    #input-message {
        width: 100%;
        margin: auto;
        padding-top: 0px;
        position: relative;
    }
    .clear-img{
      position: absolute;
      right: 20px;
      width: 20px;
      z-index: 2;
      cursor: pointer;
      height: auto;
      top: 10px;
    }
    .bot-message,
    .user-message {
      font-size: 16px;
        border-radius: 15px;
        margin-bottom: 5px;
        padding: 11px;
        white-space: pre-wrap;
    }

    .bot-message {
        text-align: left;
        float: left;
        background-color: #F7F7F7;
        max-width: 89%;
        margin-left:10px;
    }
    ::-webkit-scrollbar{
      width: 0px;
    }
    .user-message {
      float: right;
      background-color: #95EB69;
      max-width: 390px;
      margin-right:10px;
    }
    .user-icon{
      width: 45px;
      float: right;
      height: 45px;
      border-radius: 4px;
      overflow: hidden;
    }
    .bot-icon{
      width: 45px;
      float: left;
      height: 45px;
      overflow: hidden;
      border-radius: 4px;
    }
    .bot-icon img,.user-icon img{
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
    }
    ::v-deep .el-input__inner:focus{
      border: #000 1px solid;
    }
    >>> .el-switch__label *{
      font-size: 13px;
    }
   >>>  .el-switch__label.is-active{
      color: #d56bd7;
    }
    .message-div{
      float: left;
      width: 100%;
    }
    .coppy-right-text{
      margin-top: 15px;
      width: 100%;
    }
    @media only screen and (min-width: 320px) and (max-width: 479px){
      .main{
        width: 100%;
      }
      #chat-container{
        height: 520px;
      }
      .bot-message{
        max-width: 72%;
      }
      #input-message{
        padding: 0 2px;
      }
      .coppy-area{
        padding: 0;
      }
    }

</style>




