<template>
    <div class="content">
        <div class="main">
<div class="right">
  <div class="tips-text">注：文字配音为实时生成，服务器不会储存生成结果，请及时下载并自行保存您生成的配音文件。</div>
  <div class="text1">输入配音文案。（剩余<span style="color:red;font-weight: bold;font-size:16px;">{{voice_words}}</span>字）</div>
  <div :class="{'slide-wrap':true,'active':upType=='voice'}">
    <div class="slide-item" >
      <el-input
