            <el-tab-pane label="系统配音员" name="sys">
              <div class="demo-input-suffix">
                <span class="audio_title">文案配音：</span>
                <div style="display: inline-block;" @click.capture="centerDialogVisible = true">   
                  <el-input
                    style=" width: 200px;"
                    placeholder="请选择配音"
                    readonly
                    suffix-icon="el-icon-arrow-right"
                    v-model="audio">
                    <img class="audioIcon" slot="prefix" :src="audioIcon"></img>
                  </el-input>
                </div>
              </div>
            </el-tab-pane>
            <el-tab-pane label="声音克隆"  name="user">
              <div style="height:200px;overflow:scroll">
                  <div class="flex-wrap-clone">
                    <div v-for="item in audioCloneList" @click="audioCloneItemClick(item)" :class="{'clone-item':true,'active':activeCloneItem.value ==item.value}">
                      <div class="text">{{ item.label }}</div>
                      <i class="el-icon-video-play icon" v-if="!isPlaying || playingAudio!=item.value" @click.stop="playAudio(item,'mine')"></i>  
                      <i class="el-icon-video-pause icon"  v-if="isPlaying&&playingAudio==item.value" @click.stop="pauseAudio()"></i>  
                    </div>
                    <div v-if="audioCloneList.length<1" class="empty">您还没有定制专属自己的声音角色哦，点<span @click="gotoHis()">这里</span>去上传。</div>
                  </div>
              </div>
            </el-tab-pane>
          </el-tabs>
      </div>
    </div>
  </div>

  <div id="input-message">
    <el-button class="button-up" :disabled="buDisabled"  @click="sendMessage()">立即生成</el-button>
    
    <i class="el-icon-video-play icon" v-if="audioUrl && !isPlayingRes" @click="playAudioRes()"></i>  
      <i class="el-icon-video-pause icon"  v-if="audioUrl && isPlayingRes" @click="playAudioRes()"></i>  
    <i class="el-icon-download" v-if="audioUrl" @click.stop="downloadFile()"></i>
  </div>
    <div class="user-notice"><el-checkbox v-model="checked"></el-checkbox> 我已阅读<span @click="isNotice = true"> 《用户协议》 </span><span @click="isPrivacy = true"> 《用户隐私声明》 </span></div>
</div>
</div>
<el-dialog
  title="文案配音"
  :visible.sync="centerDialogVisible"
  width="730px">
  <span slot="title" class="dia_header">文案配音</span>
  <div><span v-for="item in audioClass" :key="item.value" @click="audioClassClick(item)" :class="{'man_audio':true,'active':activeAudio==item.value}">{{ item.label }}</span></div>
  <div class="flex-wrap">
    <div v-for="item in audioAllList.filter(i=>i.class == activeAudio)" @click="audioItemClick(item)" :class="{'audio-item':true,'active':activeItem.value ==item.value}"><div class="img" :style="backStyle(item)"></div>
      <div class="text">{{ item.label }} - {{ item.lang }}</div><span v-if="item.badge" class='badge_a'>{{ item.badge }}</span>
      <i class="el-icon-video-play icon" v-if="!isPlaying || playingAudio!=item.value" @click.stop="playAudio(item,'sys')"></i>  
      <i class="el-icon-video-pause icon"  v-if="isPlaying&&playingAudio==item.value" @click.stop="pauseAudio()"></i>  
    </div>
  </div>
  <span slot="footer" class="dialog-footer">
    <el-button size="small" @click="centerDialogVisible = false">取 消</el-button>
    <el-button size="small" type="primary" @click="submitAudio">确 定</el-button>
  </span>
</el-dialog>
<el-dialog
  title=""
  :visible.sync="isNotice"
  width="1350px">
  <Notice/>
  <span slot="footer" class="dialog-footer">
    <el-button size="small" type="primary" @click="isNotice=false">确 定</el-button>
  </span>
</el-dialog>
<el-dialog
  title=""
  :visible.sync="isPrivacy"
  width="1350px">
  <Privacy/>
  <span slot="footer" class="dialog-footer">
    <el-button size="small" type="primary" @click="isPrivacy=false">确 定</el-button>
  </span>
</el-dialog>
<el-dialog
  title=""
  :visible.sync="openDocsDialog"
  width="1350px">
  <Docs/>
  <span slot="footer" class="dialog-footer">
    <el-button size="small" type="primary" @click="openDocsDialog=false">确 定</el-button>
  </span>
</el-dialog>
    </div>
</template>
<script>
import { dub_voice_v1,get_audios,get_voice_words,tts_set_audio,get_tts_userInfo,tts_delete_roles } from '@/api/api.js'
import Docs from '@/view/docs'
import Notice from '@/view/components/notice'
import Privacy from '@/view/components/privacy'
export default {
    name: 'DigitalHuman',
    components:{Docs,Notice,Privacy},
    data(){
        return{
        videoPath: process.env.VUE_APP_API_BASE_URL + '/static/image/video/exam_gan.mp4',
        posterImg:'',
        checked:false,
        isNotice:false,
        isPrivacy:false,
        buDisabled:false, 
        size: 0,
        isFocus:false,
        activeAudio:'man',
        tabAudio:'sys',
        audioClass:[
          {
            label:'男声',
            value:'man'
          },{
            label:'女声',
            value:'woman'
          }
        ],
        activeCloneItem:{},
        audioCloneList:[
        
        ],
        audioAllList:[
          {
            label:'通用女声',
            value:'BV001_streaming',
            class:'woman',
            badge:'Hot',
            lang:'中文'
          },
          {
            label:'通用男声',
            value:'BV002_streaming',
            class:'man',
            badge:'',
            lang:'中文'
          },
          {
            label:'亲切女声',
            value:'BV007_streaming',
            class:'woman',
            badge:'hot',
            lang:'中文'
          },
          {
            label:'重庆小伙',
            value:'BV019_streaming',
            class:'man',
            badge:'Hot',
            lang:'重庆话'
          },
          {
            label:'活泼女声',
            value:'BV005_streaming',
            class:'woman',
            badge:'Hot',
            lang:'中文'
          },
          {
            label:'活力男声-Jackson',
            value:'BV504_streaming',
            class:'man',
            badge:'',
            lang:'英语'
          },
          {
            label:'古风少御',
            value:'BV115_streaming',
            class:'woman',
            badge:'',
            lang:'中文'
          },
          {
            label:'擎苍',
            value:'BV701_streaming',
            class:'man',
            badge:'',
            lang:'中文'
          },
          {
            label:'奶气萌娃',
            value:'BV051_streaming',
            class:'man',
            badge:'',
            lang:'中文'
          },
          {
            label:'甜宠少御',
            value:'BV113_streaming',
            class:'woman',
            badge:'',
            lang:'中文'
          },
          {
            label:'阳光男声',
            value:'BV056_streaming',
            class:'man',
            badge:'',
            lang:'中文'
          },
          {
            label:'温柔小哥',
            value:'BV033_streaming',
            class:'man',
             badge:'',
            lang:'中文'
          },
          {
            label:'儒雅青年',
            value:'BV102_streaming',
            class:'man',
             badge:'',
            lang:'中文'
          },
          {
            label:'气质女生',
            value:'BV522_streaming',
            class:'woman',
             badge:'',
            lang:'日语'
          },
          {
            label:'通用赘婿',
            value:'BV119_streaming',
            class:'man',
             badge:'',
            lang:'中文'
          },
          {
            label:'日语男声',
            value:'BV524_streaming',
            class:'man',
             badge:'',
            lang:'日语'
          },
          {
            label:'广西表哥',
            value:'BV213_streaming',
            class:'man',
             badge:'',
            lang:'广西普通话'
          },
          {
            label:'知性姐姐-双语',
            value:'BV034_streaming',
            class:'woman',
             badge:'',
            lang:'中文'
          },
          {
            label:'东北老铁',
            value:'BV021_streaming',
            class:'man',
             badge:'',
            lang:'东北话'
          },
          {
            label:'活力女声-Ariana',
            value:'BV503_streaming',
            class:'woman',
             badge:'',
            lang:'英语'
          },
          {
            label:'灿灿',
            value:'BV700_streaming',
            class:'man',
             badge:'',
            lang:'中文'
          },
          {
            label:'炀炀',
            value:'BV705_streaming',
            class:'man',
             badge:'',
            lang:'中文'
          },
        ],
        centerDialogVisible:false,
        audio:'亲切女声-中文',
        message:'',
        audioIcon:require('@/assets/img/woman.png'),
        manPic:require('@/assets/img/man.png'),
        womanPic:require('@/assets/img/woman.png'),
        showLoading:null,
        voice_words:0,
        audioUrl:'',
        activeItem: {
            label:'亲切女声',
            value:'BV007_streaming',
            class:'woman',
            badge:'hot',
            lang:'中文'
          },
        upType:'text',
        examImg:[false,false,false],
        openDocsDialog:false,
        audioHtml:'',
        audioHtmlRes:'',
        isPlaying:false,
        isPlayingRes:false,
        playingAudio:'',
        }
    },
    created(){
      this.get_words()
    },
    mounted(){

    },
    methods:{
      get_words(){
        get_voice_words().then(res=>{
        if(res.code==200){
          this.voice_words = res.data.voice_words
        }
      })
      
      },
      gotoHis(){
        this.$emit('goVoice')
      },
      openDocs(){
        this.openDocsDialog = true
      },
      tabClick(tab){
        if(this.tabAudio == 'user'){
            let data = new FormData()
              data.append('page',1)
              data.append('rows',999)
            get_audios(data).then(res=>{
              this.audioCloneList=res.data.map(i=>{
                return {
                  label:i.voice_name,
                  id:i.id,
                  value:i.voice_id,
                  url:i.prompt_url,
                  class:'mine',
                  lang:'中文'
                }
              })
          //      {
          //   label:'通用女声',
          //   value:'BV001_streaming',
          //   class:'woman',
          //   lang:'中文'
          // },
            })
          
        }
      },
      downloadFile(){
        const ele = document.createElement('a')
        ele.setAttribute('href',this.audioUrl)
        ele.setAttribute('download','')
        ele.click()
      },
      playAudioRes(){
        if(this.audioHtmlRes){
          if(this.isPlayingRes){
            this.audioHtmlRes.pause()
          }else{
            this.audioHtmlRes.play()
          }
          this.isPlayingRes = !this.isPlayingRes
          console.log(this.isPlayingRes)
        }else{
          this.isPlayingRes = true
          var url = this.audioUrl
          this.audioHtmlRes = new Audio();
          this.audioHtmlRes.src = url
          this.audioHtmlRes.addEventListener("canplaythrough",event => {
            this.audioHtmlRes.play()
          // this.audioHtml.pause()
            //this.audioHtml.paused
          })
          console.log(this.isPlayingRes)
        }
        
      },
      playAudio(item,type){
        if(this.playingAudio == item.value && this.audioHtml){
          this.audioHtml.play()
          this.isPlaying = true
        }else{
          if(this.audioHtml){
            this.audioHtml.pause()
          }
          this.isPlaying = true
          this.playingAudio = item.value
          var url = process.env.VUE_APP_API_BASE_URL + 'static/audio/'+item.value+'.mp3'
          if(type == 'mine'){
            url = item.url
          }
          this.audioHtml = new Audio();
          this.audioHtml.src = url
          this.audioHtml.addEventListener("canplaythrough",event => {
            this.audioHtml.play()
          // this.audioHtml.pause()
            //this.audioHtml.paused
          })
        }

      },
      pauseAudio(){
        if(this.audioHtml){
          this.audioHtml.pause()
          this.isPlaying = false
        }
      },
      backStyle(item){
        let pic = ''
        if(item.class=='man'){
          pic = this.manPic
        }else{
          pic = this.womanPic
        }
        return {
          backgroundImage: `url(${pic})`
        }
      },
      audioClassClick(item){
        this.activeAudio = item.value
      },
      audioItemClick(item){
        this.activeItem = item
      },
      audioCloneItemClick(item){
        this.activeCloneItem = item
      },
      submitAudio(){
        this.audio = this.activeItem.label + '-' + this.activeItem.lang
        if(this.activeItem.class=='man'){
          this.audioIcon = this.manPic
        }else{
          this.audioIcon = this.womanPic
        }
        this.centerDialogVisible = false
      },
      choose(num){
        this.examImg.splice(num,1,true)
        this.examImg.forEach((i,index)=>{
          if(index !== num){
            this.examImg.splice(index,1,false)
          }
        })
      },
      refuse(num){
        this.examImg.splice(num,1,false)
      },
    async sendMessage() {
      //this.showLoading=true
      let formData = new FormData();
      if(this.upType =='text'){
        if(!this.message){
          this.$message.error('请输入需要配音的文案！');
          return;
        }
        if(this.tabAudio == 'user' && !this.activeCloneItem['id']){
          this.$message.error('请选择配音方案！');
          return;
        }
        if(this.tabAudio == 'user'){
            formData.append('voice_clone',this.activeCloneItem['value'])
          }else{
            formData.append('voice',this.activeItem.value)
          }
          formData.append('userMessage',this.message)
                              //4个字1s，15s一积分
        }

        if(!this.checked){
          this.$message.error('请仔细阅读用户协议和隐私声明！');
            return;
        }
        this.buDisabled = true;
        this.audioUrl = '';
        this.$confirm('生成音频预计消耗'+this.message.length+'文字点数,是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.showLoading = this.$loading({
            lock: true,
            text: 'Loading',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
          });
          // let obj = {
          //   userMessage:'创建新的语言角色', //tts_set_audio
          //   voice_id:'a0b9c1c3-dfda-4e99-92c4-1775ad622514'

          // }
          this.audioHtmlRes = ''
          dub_voice_v1(formData).then(response => {
            // var botMessageDiv = document.createElement("div");
            // botMessageDiv.classList.add("bot-message");
            // botMessageDiv.innerText = response;
            if(response.code == 200){
                // this.$store.dispatch('setCredit')
                  this.audioUrl = process.env.VUE_APP_API_BASE_URL + response.data.audio_path.replace('core','');
                  this.voice_words = response.data.voice_words;
                  this.$message.success(response.msg);

                // this.videoPath =process.env.VUE_APP_API_BASE_URL + response.video_path
                // this.posterImg =process.env.VUE_APP_API_BASE_URL+ response.image_path
                
                this.message = "";
             }
            this.buDisabled = false;
            this.showLoading.close();
            // chatContainer.appendChild(botMessageDiv);
          })
          .catch(error => {
              this.buDisabled = false;
              this.showLoading.close();
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          });
          this.buDisabled = false;          
        });
        
        },
    }
}
</script>
