  overflow: hidden;
  padding-top: 20px;
  margin: 5px 0 0 2%;
  height: 700px;
  background: #fff;
}
.right>*{
  width: 90%;
  margin: 0 auto;
}
.segment{
  height: 50px;
  background: #f4f5f8;
  border-radius: 25px;
  position: relative;
  margin: 20px auto;
  z-index: 1;
}
.segment-item{
  display: inline-block;
  width: 50%;
  text-align: center;
  line-height: 50px;
  cursor: pointer;
  color: #666;
  z-index: 1;
  font-size: 14px;

}
.transition{
  transition-property: transform, opacity, height, background-color, border-color;
  transition-timing-function: cubic-bezier(.4,0,.2,1);
  transition-duration: .15s;
  position: absolute;
  top: 4px;
  z-index: -1;
  bottom: 0;
  display: inline-block;
  width: 48%;
  left: 5px;
  height: 42px;
  border-radius: 38px;
  background-color: #fff;
}
.transition.active{
  transform: translateX(258px);
}
.right .slide-wrap{
  width: 100%;
  overflow: hidden;
 margin-top: 20px;
}
.slide-item{
  padding: 0 0%;
  width: 100%;
  float: left;
  height: 470px;
}
.right .slide-wrap.active{
  transform: translateX(-50%);
}
.audio{
  margin: 35px auto 0 auto;
  height: 40px;
  padding-top: 20px;
  border-top: 1px solid #f4f5f8;
}
.font-bold{
  font-weight: bold;
  font-size: 14px;
}
.demo-input-suffix{
  font-size: 14px;
}
.audio_title{
  width: 98px;
  display: inline-block;
  text-align: right;
}
.dia_header{
  height: 40px;
  line-height: 40px;
  text-align: left;
  display: block;
  width: 100%;
  border-bottom: 1px solid #f2f3f5;
}
.audioIcon{
  width: 24px;
  height: auto;
  margin-top: 7px;
}



#input-message {
     margin: 30px 0 0 150px;
     display: flex;
    text-align: center;
}
#input-message>i{
  font-size: 50px;
  margin-left: 10px;
  cursor: pointer;
}
.user-notice{
 
   padding-left: 100px;
}
.user-notice span{
  color: #2d8af5;
  cursor: pointer;
  text-decoration: underline;
}
::-webkit-scrollbar{
  width: 0px;
}


::v-deep .el-input__inner:focus{
  border: #000 1px solid;
}
>>> .el-switch__label *{
  font-size: 13px;
}
>>> .el-switch__label.is-active{
  color: #d56bd7;
}
.coppy-right-text{
  margin-top: 15px;
  width: 100%;
  float: left;
}
@media screen and (max-width: 600px) {

    #select-message select {
        width: 40%;
        height: 50px;
        font-size:16px;
        padding: 10px;
        border-radius: 8px;
    }
    #input-message input {
        width: 60%;
        height: 50px;
        font-size:16px;
        padding: 10px;
        border-radius: 8px;
    }
}

.padding-0{
padding:0 5px!important;
}
.coppy-right-text p {
    text-align: center;
    color: #999D9C;
    font-size: 17px;
    font-weight: 500;
    margin: 0;
}
.el-icon-warning{
  cursor: pointer;
}
</style>




