<template>
<div class="main">
  <div>
    <div class="text1">输入需要配音的对话文本，或者直接上传文本照片。</div>
    <div class="left">
      <div>
        <el-input
          placeholder="请输入文本"
          v-model="message"
          type="textarea"
          maxlength="2000"
          show-word-limit
          :rows="15"
          @keyup.enter.native="sendMessageE"
          clearable>
        </el-input>

        <!-- <div style="width: 15px;"></div>-->
        <!--<el-button type="warning" class="padding-0" >Clear</el-button> -->
      </div>
    </div>
    <div class="right">
      <div class="file-body">
        <el-upload
          class="upload-demo"
          drag
          accept=".png,.jpg,.jpeg,.pneg"
          :limit="1"
          :auto-upload="false"
          :on-change="changeAudioUser"
          :on-remove = "removeAudioUser"
          :file-list="fileList"
          action=""
          multiple>
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">将您的文件拖到此处，或<em>点击上传</em></div>
          <div class="el-upload__tip" slot="tip">只能上传.png,.jpg,.jpeg,.pneg文件，且不超过10Mb</div>
        </el-upload>
        <el-button class="button-form" :disabled="buDisabledForm"  @click="getWord()">提取文字</el-button>
      </div>
    </div>
    <div id="input-message">
      <el-button class="button-up" :disabled="buDisabled"  @click="nextStep()">生成音频</el-button>
      <i class="el-icon-video-play icon" v-if="audioUrl && !isPlayingRes" @click="playAudioRes()"></i>  
      <i class="el-icon-video-pause icon"  v-if="audioUrl && isPlayingRes" @click="playAudioRes()"></i>  
    <i class="el-icon-download" v-if="audioUrl" @click.stop="downloadFile()"></i>
    </div>
  </div>
</div>
</template>
<script>
import { trans_pic } from '@/api/hsyq/index'
import { set_chat_audio } from '@/api/api'
import Chatbot from "@/view/components/history/chatbot";
export default {
    name: 'ChatVideo',
    components: {
      // PollDetail,
      Chatbot
    },
    data(){
        return{
          message:'',
          buDisabled:false,
          buDisabledForm:false,
          showLoading:null,
          fileList:[],
           audioHtmlRes:'',
          audioUrl:'', //音频地址
          isPlayingRes:false,
        }
    },
    created(){
     
    },
    mounted(){
  
    },
    methods: {
      getWord(){
        if(!this.fileList.length){
          this.$message.error('请上传文件')
          return
        }
        this.buDisabledForm = true
        this.showLoading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
          
        let file = this.fileList[0].raw,result = null
        console.log(file)
        if (file) {
          const reader = new FileReader();
          reader.onload = (e) => {
            console.log(e)
            result = e.target.result;
            console.log(result)
            let json = {
          "prompt": "",
          "max_tokens": 2048,
          "temperature": 0.3,
          "top_p": 0.9,
          "model": "yi-vision",
          "messages": [
            {
                  "role": "user",
                  "content": [
                      {
                          "type": "text",
                          "text": " 帮我提取图片中的英文，原文英文输出给我，不要做任何改变"
                      },
                      {
                          "type": "image_url",
                          "image_url": {
                              "url": result
                          }
                      }
                  ]
              }
            ],
            "tools": null
        }
        trans_pic(json).then(res=>{
          console.log(res)
          this.message = res.choices[0].message.content
           this.buDisabledForm = false
        this.showLoading.close();
        })
          };
          reader.readAsDataURL(file);
        }
        
        
      },
      downloadFile(){
        const ele = document.createElement('a')
        ele.setAttribute('href',this.audioUrl)
        ele.setAttribute('download','')
        ele.click()
      },
      playAudioRes(){
        if(this.audioHtmlRes){
          if(this.isPlayingRes){
            this.audioHtmlRes.pause()
          }else{
            this.audioHtmlRes.play()
          }
          this.isPlayingRes = !this.isPlayingRes
          console.log(this.isPlayingRes)
        }else{
          this.isPlayingRes = true
          var url = this.audioUrl
          this.audioHtmlRes = new Audio();
          this.audioHtmlRes.src = url
          this.audioHtmlRes.addEventListener("canplaythrough",event => {
            this.audioHtmlRes.play()
          // this.audioHtml.pause()
            //this.audioHtml.paused
          })
          console.log(this.isPlayingRes)
        }
        
      },
      nextStep(){
        if(!this.message){ 
          this.$message.error('请输入内容')
          return
        }
        this.showLoading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        this.audioUrl='' //音频地址
        this.isPlayingRes=false
        let data = {
          text: this.message,
        }
        this.audioHtmlRes = ''
       set_chat_audio(data).then(res=>{
         console.log(res)
         if(res.code==200){
          this.audioUrl= process.env.VUE_APP_API_BASE_URL + res.data.url.replace('core','') //音频地址
          this.isPlayingRes=true
          this.showLoading.close();
         }else{
          this.$message.error(res.msg)
          this.showLoading.close();
         }
          
       }).catch(error => {
              this.showLoading.close();
          });

      },
      changeAudioUser(file, fileList){
        this.fileList = fileList
      },
      removeAudioUser(){
        this.fileList = []
      }
    }

}
</script>
<style scoped>
    .main{
      width: 1080px;
    }
    .chat{
      width: 1080px;
    }
   >>> #input-message .el-button{
      border-radius:20px;
    }
   .text1{
    font-size: 15px;
    width: 100%;
    font-weight: 700;
    padding-left: 2px;
  }
  .el-icon-back{
    font-size: 25px;
  }
    .left{
      width: 69%;
      padding-top: 12px;
      float: left;
      margin: 0px auto;
      background: #fff;
      border-radius:20px;
      padding-bottom:15px;
      overflow: hidden;
    }
    .right{
      width: 30%;
      float: right;
      margin-top: 2px;
      background: #fff;
      height: auto;
      border-radius:20px;
      overflow: hidden;
    }
    >>> .left .el-textarea__inner{
      
      padding:5px 80px 5px 15px ;
    }
    >>> .left .el-textarea{
      font-size: 16px ;
    }

    #input-message {
        width: 100%;
        margin: auto;
        text-align: center;
        float: right;
        padding-top: 25px;
        position: relative;
    }
    #input-message>i{
  font-size: 50px;
  margin-left: 10px;
  cursor: pointer;
}
    .button-form{
      line-height:0px;
      width: 93px;
      margin:10px 0 0 100px;
      height: 28px;
      font-weight: 700;
      margin-bottom: 8px;
    }
  .button-up{
    background-image:linear-gradient(106.92deg, #10c5fe 14.83%, #1093fe 31.52%, #4f4fe7 65.11%, #b60cb9 91.29%, #e0caf5 102.83%)!important;
    color: #ffffff!important;
    width: 323px;
    height: 58px;
    border-radius: 29px!important;
    font-size: 18px !important;
    font-weight: 700;
    margin-bottom: 8px;
  }
    ::-webkit-scrollbar{
      width: 0px;
    }
    
    ::v-deep .el-input__inner:focus{
      border: #000 1px solid;
    }
    .file-body{
      padding: 10px;
    }
    >>> .right .el-upload, >>> .right .el-upload-dragger{
      width: 100%;
    }
    >>> .el-upload__input{
      display: none;
    }
    >>> .el-switch__label *{
      font-size: 13px;
    }
   >>>  .el-switch__label.is-active{
      color: #d56bd7;
    }
    .message-div{
      float: left;
      width: 100%;
    }
    .coppy-right-text{
      margin-top: 15px;
      width: 100%;
    }
    @media only screen and (min-width: 320px) and (max-width: 479px){
      .main{
        width: 100%;
      }
      #chat-container{
        height: 520px;
      }
      .bot-message{
        max-width: 72%;
      }
      #input-message{
        padding: 0 2px;
      }
      .coppy-area{
        padding: 0;
      }
    }

</style>




