import request from '../index.js'

export function hsyq_upload(data){
  return request({
    url:  '/tts/voice',
    method: 'POST',
    headers: {
      'Authorization': 'Bearer sk-2e0aaf36a6d8897478d17785736f1386'
    },
    data
  })
}
export function get_upload(){
  return request({
    url:  '/tts/voice',
    method: 'GET',
    headers: {
      'Authorization': 'Bearer sk-2e0aaf36a6d8897478d17785736f1386'
    },
  })
}
export function trans_pic(data){
  return request({
    url:  '/v1/chat/completions',
    method: 'POST',
    headers: {
      'Authorization': 'Bearer 633158349771440d9054e705316ca0c3'
    },
    data
  })
}



