<template>
    <div class="content">
          <div class="main">
            <div class="title">我的声音 <span>{{'(声音角色上限:'+voiceTimes}}</span><span style="padding-left:10px">{{'删除/重新上传声音角色次数剩余:'+deleteVoiceTimes+')'}}</span></div>
            <div class="button"><el-button type="primary" size="small" @click="createVoice">创建声音</el-button> </div>
            <div>
               <el-table
              :data="tableData"
              style="width: 100%">
              <el-table-column
                type="index"
                :index="indexMethod"
                label="序号"
                width="180">
              </el-table-column>
              <el-table-column
                prop="voice_name"
                label="名称"
                width="180">
              </el-table-column>
              <el-table-column
                prop="prompt_url"
                label="音频文件">
                <template slot-scope="scope">
                  <audio :src="scope.row.prompt_url" controls="controls" style="height:30px;"></audio>
                </template>
              </el-table-column>
              <el-table-column
                prop=""
                label="操作">
                <template slot-scope="scope">
                  <el-button type="text" @click="deleteVoice(scope.row)" size="small" style="color:red;">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
             <div class="block">
              <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[10, 20, 50]"
                :page-size="10"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
              </el-pagination>
            </div>
            </div>
          </div>
          <el-dialog
          :visible.sync="diglogVisible"
          :close-on-click-modal="false"
          width="680px">
          <span class="dia_header">声音上传</span>
          <div class="upload_body">
            <el-form ref="form" :model="form" :rules="rules" label-position="left" label-width="80px">
              <el-form-item label="名称" prop="name">
                <el-input v-model="form.name"></el-input>
              </el-form-item>
              <el-form-item label="声音文件"  prop="audioList">
                <el-upload
                  class="upload-demo"
                  drag
                  accept=".mp3,.webm,.wav,.ogg,.m4a"
                  :auto-upload="false"
                  :on-change="changeAudio"
                  :on-remove = "removeAudio"
                  :file-list="form.audioList"
                  action=""
                  multiple>
                  <i class="el-icon-upload"></i>
                  <div class="el-upload__text">将音频文件拖到此处，或<em>点击上传</em></div>
                  <div class="el-upload__tip" slot="tip">推荐上传一段5s-20s的高质量的样本语音，且不超过10Mb。并且，请确保音频包含可被识别的中文或英文语句，否则可能导致创建失败等问题。</div>
                </el-upload>
              </el-form-item>
            </el-form>

           
          </div>
          

            <div class="sub-button">
              <el-button  type="primary" @click="submitForm('form')">确定</el-button>
              <el-button  type="" @click="diglogVisible= false">关闭</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
import { get_audios,set_audios ,delete_audios,tts_set_roles} from '@/api/api.js'
export default {
    name: 'HistoryVoice',
    data(){
        return{
          videoList:[
          ],
          rules:{
            name:[{required:true,message:'请输入名称',trigger:'blur'}]
          },
          baseUrl:process.env.VUE_APP_API_BASE_URL,
          diglogVisible:false,
          voiceTimes:0, 
          deleteVoiceTimes:0,
          tableData: [],
          currentPage:1,
          pageSize:10,
          loading:null,
          total:0,
          form:{
            name:'',
            audioList:[],
          }
        }
    },
    mounted(){
      this.get_audios()
      console.log(this.$store.state.userInfo)
    },
    methods:{
      get_audios(){
        console.log(1111)
        let data = new FormData()
          data.append('page',this.currentPage)
          data.append('rows',this.pageSize)
        get_audios(data).then(res=>{
          this.tableData=res.data
          this.voiceTimes = res.audio_times
          this.deleteVoiceTimes = res.delete_times
          this.total=res.total
          console.log(res)
        })
      },
      submitForm(formName) {
      this.loading =  this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });

      //  this.$message.error('该功能暂未上线');
        this.$refs[formName].validate((valid) => {
          if (valid) {
            if(this.form.audioList.length==0){
              this.$message.error('请上传音频文件');
              return;
            }
            this.hsyq_upload()
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      fileToBase64(file) {
        return new Promise((resolve, reject) => {
          const reader = new FileReader();
          reader.readAsDataURL(file);
          reader.onload = () => resolve(reader.result);//.split(',')[1]
          reader.onerror = error => reject(error);
        });
      },
      hsyq_upload(){
        this.fileToBase64(this.form.audioList[0].raw)
        .then(base64 => {
          let data = {
            // appid:"8532245923",
            // speaker_id:'S_bJjhlUBQ',
            // audios:[{
            //    "audio_bytes":base64,
            //    "audio_format":this.form.audioList[0].name.split('.')[1]
            // }],
            // source:2
            name:this.form.name,
            prompt:base64
          }
                  
        set_audios(data).then(res=>{
          if(res.code==200){
            this.$message.success('上传成功');
            this.form = {
              name:'',
              audioList:[],
            }
            this.get_audios()
            this.diglogVisible = false;
          }


         this.loading.close();
        }).catch(error => this.loading.close());
        })
        .catch(error => this.loading.close());
       

      },
      changeAudio(file, fileList){
        if(fileList.length>1){
          fileList.splice(0,1)
        }
        this.form.audioList = fileList
      },
      removeAudio(file, fileList){
        this.form.audioList = []
      },
      createVoice(){
        this.diglogVisible = true
      },
      deleteVoice(item){
        console.log(item)
        this.$confirm('该账户还有 '+ this.deleteVoiceTimes+' 次删除机会，此操作将永久删除该声音角色, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let param = new FormData();
          param.append('id',item.id)
          param.append('voice_id',item.voice_id)
          delete_audios(param).then(res=>{
            if(res.code==200){
              this.$message.success('删除成功');
              this.get_audios()
            }
          })
        }).catch(() => {
          
        })
      },
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
        this.pageSize = val
        this.get_audios()
      },
      indexMethod(index) {
        return index + 1;
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        console.log(`当前页: ${val}`);
        this.get_audios()
      }
    }
}
</script>
<style scoped>
.upload_body{
  width: 70%;
  margin: 30px auto;
}
.content{
  width: 100%;
  min-height: 700px;
  background:#fff;
  border-top: 1px solid #f4f5f7;
}
#player1{
  margin-top: 20px;
  display: block;
  margin: 0 auto;
}
.main{
  padding: 16px;
  min-width: 830px;
}
>>> .el-dialog{
  border-radius: 10px;
}
>>> .el-form-item__content{
  line-height: normal;
}
.dia_header{
  font-size: 18px;
  height:28px;
  font-weight: bold;
  line-height: 28px;
  width: 100%;
  display: inline-block;
  padding-left: 15%;
}

.main .title{
  font-size: 18px;
  font-weight: bold;
}
.button{
  margin: 10px 0;
}
.button>*{
      background-image:linear-gradient(106.92deg, #10c5fe 14.83%, #1093fe 31.52%, #4f4fe7 65.11%, #b60cb9 91.29%, #e0caf5 102.83%)!important;
      border: none;
      color: #ffffff!important;
}
.main .title span{
  font-size: 14px;
    color: red;
  }

.sub-button{
  margin: 20px 0 0 0;
  text-align: center;
}
>>> .upload_body .el-upload, >>> .upload_body .el-upload-dragger{
  width: 100%;
}
>>> .el-upload__input{
  display: none;
}
 >>>.sub-button .el-button{
  width: 100px;
}
.row{
    margin-right: 0;
    margin-left: 0;
}
.CompanyIntroduction-container{
    padding: 100px 0;
    color: #808080;
    transition: all ease 0.5s;
}
.CompanyIntroduction-container>div>p{
    font-size: 14px;
    line-height: 2.5rem;
}
@media screen and (max-width: 997px){
    .CompanyIntroduction-container{
    padding: 10px 0;
    color: #808080;
}
}
</style>




