<script>
import { WOW } from 'wowjs';
import { getWorks,deleteWork,mylogin } from '@/api/api.js'
import HisVideo from "@/view/components/history/video";
import HisVoice from "@/view/components/history/voice";
import Dub from "@/view/components/history/dub";
import English from "@/view/components/history/English";
import chatVideo from "@/view/components/history/chatVideo";
import Bill from "@/view/components/history/bill";
import {getAuth, signInWithPopup, GoogleAuthProvider,onAuthStateChanged  } from "firebase/auth";
export default {
    name: 'History',
    components: {
      // PollDetail,
      HisVideo,
      HisVoice,
      Dub,
      English,
      chatVideo,
      Bill
    },
    data(){
        return{
          activeLi:'work',
          videoList:[
          ],
          baseUrl:process.env.VUE_APP_API_BASE_URL,
          diglogVisible:false,
          isLogin:false,
          activeItem:{
            create_time:'',
            id:'',
            input_text:'null',
            output_img_path: "",
            output_video_path: ""
          }
        }
    },
    created(){
      
    },
    mounted(){
        var wow = new WOW();
        wow.init();
        this.checkGoogleLogin() 
        // this.getWorks()
    },
    methods:{
      goVoice(){
        this.activeLi = 'voice'
      },
      login(){
      const auth = getAuth();
      const provider = new GoogleAuthProvider();
      signInWithPopup(auth, provider)
        .then((result) => {
          // This gives you a Google Access Token. You can use it to access the Google API.
          const credential = GoogleAuthProvider.credentialFromResult(result);
          const token = credential.accessToken;
          // The signed-in user info.
          const user = result.user;
          this.$store.commit('SET_USER',user.reloadUserInfo)
          mylogin(user.reloadUserInfo)
          this.isLogin = true;
          console.log(user)
        }).catch((error) => {

          // Handle Errors here.
          const errorCode = error.code;
          const errorMessage = error.message;
          // The email of the user's account used.
          const email = error.email;
          // The AuthCredential type that was used.
          const credential = GoogleAuthProvider.credentialFromError(error);
        });
      },
      checkGoogleLogin() {
        const auth = getAuth();
      // 尝试获取当前用户
      onAuthStateChanged(auth, (user) => {
        if (user) {
          // 用户已登录
          this.$store.commit('SET_USER',user.reloadUserInfo)
          mylogin(user.reloadUserInfo)
          this.isLogin = true;
          console.log("用户已登录", user);
          console.log( this.$store.state.userInfo)
        } else {
          // 用户未登录
          console.log("用户未登录");
        }
      });
      // console.log(auth);
      //   const user = auth.currentUser;
      // console.log(user);
      //   if (user) {
      //     // 用户已登录
      //     console.log("用户已登录", user);
      
      //     // 检查用户的身份提供者
      //     if (user.providerData.length > 0) {
      //       user.providerData.forEach((profile) => {
      //         if (profile.providerId === "google.com") {
      //           // 用户是通过Google账号登录的
      //           console.log("用户是通过Google登录的");
      //         }
      //       });
      //     }
      //   } else {
      //     // 用户未登录
      //     console.log("用户未登录");
      //   }
      },
      deleteWork(id){
        this.$confirm('此操作将永久删除该数字人文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
          .then(() => {
            let formData = new FormData();
            formData.append('id',id)
            deleteWork(formData).then(res=>{
              if(res.code == 200){
                this.$message({
                  type: 'success',
                  message: '删除成功!'
                });
                this.getWorks()
              }
            })
        })
          .catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
        })
      },
      getTextAfterLastSymbol(str, symbol) {
          var index = str.lastIndexOf(symbol);
          return str.slice(index + 1);
      },
      preview(item){
        if(!item.output_video_path){
          return
        }
        this.diglogVisible = true
        this.activeItem=item
      },
      getWorks(){
        getWorks().then(res => {
            this.videoList = res.data.rows
        })
      },
    }
}
</script>
