<template>
    <div class="content">
          <div class="main">
            <div class="title">充值记录</span></div>
            <div>
               <el-table
              :data="videoList"
              style="width: 100%">
              <el-table-column
                type="index"
                :index="indexMethod"
                label="序号"
                width="100">
              </el-table-column>
 
              <el-table-column
                prop="create_time"
                label="创建时间"
                width="180">
                <template slot-scope="scope">
                  <span>{{scope.row.create_time.replace('T',' ')}}</span>
                </template>
              </el-table-column>
              <el-table-column
                prop="price"
                label="充值金额"
                width="150">
              </el-table-column>
              <el-table-column
                prop="add_credit"
                width="160"
                label="到账积分">
              </el-table-column>
              <!-- <el-table-column
                prop=""
                width="80"
                label="操作">
                <template slot-scope="scope">
                  <el-button type="text" @click="deleteVoice(scope.row)" size="small" style="color:red;">删除</el-button>
                </template>
              </el-table-column> -->
            </el-table>
             <div class="block">
              <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[10, 20, 50]"
                :page-size="10"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
              </el-pagination>
            </div>
            </div>
            </div>
    </div>
</template>
<script>
import { getBills,deleteWork } from '@/api/api.js'
export default {
    name: 'HistoryBill',
    data(){
        return{
          videoList:[
          ],
          currentPage:1,
          pageSize:10,
          loading:null,
          total:0,
          baseUrl:process.env.VUE_APP_API_BASE_URL,
          diglogVisible:false,
          activeItem:{
            create_time:'',
            id:'',
            input_text:'null',
            output_img_path: "",
            output_video_path: ""
          }
        }
    },
    mounted(){
      this.getBills()
    },
    methods:{
      deleteWork(id){
        this.$confirm('此操作将永久删除该数字人文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
          .then(() => {
            let formData = new FormData();
            formData.append('id',id)
            deleteWork(formData).then(res=>{
              if(res.code == 200){
                this.$message({
                  type: 'success',
                  message: '删除成功!'
                });
                this.getBills()
              }
            })
        })
          .catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
        })
      },
     handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
        this.pageSize = val
        this.getBills()
      },
      indexMethod(index) {
        return index + 1;
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        console.log(`当前页: ${val}`);
        this.getBills()
      },
      getBills(){
        getBills().then(res => {
            this.videoList = res.data.rows
            this.total = res.data.total
        })
      },
    }
}
</script>
<style scoped>

.content{
  width: 100%;
  min-height: 700px;
  background:#fff;
  border-top: 1px solid #f4f5f7;
}
#player1{
  margin-top: 20px;
  display: block;
  margin: 0 auto;
}
.work-list{
  padding:16px;
  border-right: 1px solid #f4f5f7;
}
.main{
  padding: 16px;
  min-width: 830px;
}
>>> .el-dialog{
  border-radius: 10px;
}
.dia_header{
  font-size: 18px;
  height:28px;
  line-height: 28px;
   font-weight: bold;
  width: 100%;
  display: inline-block;
}
.main .bottom{
  text-align: center;
  color: #999;
}
.main .title{
  font-size: 18px;
  font-weight: bold;
}
.main .title span{
  font-size: 12px;
    color: red;
  }
.main .video-list{
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;

  justify-content: flex-start;
}
.main .video-list li{
  cursor: pointer;
  position: relative;
  margin: 0 20px 20px 0;
  width: 350px;
  height: 250px;
}
.video_error{
  position: absolute;
  top: 65px;
  z-index: 10;
  padding: 0 10%;
  color: rgb(206, 38, 38);
  font-size: 15px;
}
.video-list .video_img{
  position: relative;
  height: 183px;
  width: 100%;
  background: linear-gradient(to bottom, #675E54, #D1D1D2);
}
.video-list li .video_blur{
  height: 0;
  overflow: hidden;
  width: 100%;
  position: absolute;
  text-align: center;
  transition: height 0.1s ease-in-out;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.3);
}
.video-list .video_img:hover .video_blur{
  height: 100%;
}
.video_blur i{
  font-size: 60px;
  color: rgba(255, 255, 255, 0.8);
  margin-top: 60px;
}
.video-list .video_img img{
  height: 100%;
  width: auto;
  margin:0 auto;
}
.video-list .li-title{
  font-size: 16px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding: 8px 15px 5px 15px;
}
 .li-creatTime{
  position: relative;
  font-size: 12px;
  color: #999;
  padding: 0px 15px;
}
.popper-ul{
  padding-top: 9px;
}
.popper-ul li{
  padding: 5px;
  text-align: center;
  cursor: pointer;
  font-size: 14px;
}
.popper-ul li:hover{
  background: #a9c6e7;
  color: #fff;
}
.popper-ul .down{
  color: #2d8af5;
}
.popper-ul .down a{
  text-decoration: none;
}
.popper-ul .delete{
  color: red;
}
.el-icon-more{
  position: absolute;
  right: 10px;
  font-size: 14px;
}
.work-list .work-li{
  padding:32px 20px;
  font-size: 16px;
  margin-bottom: 15px;
  border-bottom: 1px solid #f4f5f7;
  font-weight: bold;
}
.work-list>li{
  margin: 5px 0 0 0;
  font-size: 15px;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  height: 48px;
  border-radius: 4px;
  color: #4e5969;
  padding: 0 20px;

}
.work-li-icon{
  background: url("../../../assets/img/grzx.png") 0 0 no-repeat;
  background-size: 100% 100%;
}
.work-li-history{
  background: url("../../../assets/img/history.png") 0 0 no-repeat;
  background-size: 100% 100%;
}
.icon{
  display: inline-block;
  width: 20px;
  height: 20px;
   
  margin-right: 16px;
}
.sub-button{
  margin: 20px 0 0 0;
  text-align: center;
}
 >>>.sub-button .el-button{
  width: 100px;
}
.empty{
  margin:200px auto;
  text-align: center;
  color: #999;
}
.empty img{
    margin:4px auto;
    width: 200px;
}
.active-li:hover,.active-li.active{
    cursor: pointer;
    color: #2d8af5;
    background: #f5f9ff;
    border-radius: 4px;
}
.active-li:hover .work-li-history,.active-li.active .work-li-history{
  background: url("../../../assets/img/history_a.png") 0 0 no-repeat;
  background-size: 100% 100%;
}
.row{
    margin-right: 0;
    margin-left: 0;
}
.CompanyIntroduction-container{
    padding: 100px 0;
    color: #808080;
    transition: all ease 0.5s;
}
.CompanyIntroduction-container>div>p{
    font-size: 14px;
    line-height: 2.5rem;
}
@media screen and (max-width: 997px){
    .CompanyIntroduction-container{
    padding: 10px 0;
    color: #808080;
}
}
</style>




